<template>
  <div class="box box-primary">
    <div class="box-header with-border">
      <h3 class="box-title"></h3>
      <div class="pull-left">
        <router-link class="btn btn-primary btn-sm" v-if="editRoute" :to="editRoute">
          <i class="fa fa-save"></i> 新增
        </router-link>
        <button class="btn btn-danger btn-sm delete" v-if="showDel" @click="removeData" >
          <i class="fa fa-trash"></i> 删除</button>
        <slot name="operation"></slot>
      </div>

    </div>
    <div class="box-body table-responsive">
      <table class="table table-bordered table-hover">
        <slot name="thead"></slot>
        <slot name="tbody"></slot>
      </table>
    </div>
    <div class="box-footer clearfix">
      <pager v-if="isPager" :total="total" :page-size="pageSize" @changeSize="changeSize"
             @changePage="changePage"/>
    </div>
  </div>
</template>

<script>
  import Pager from "./Pager"
  import {mapState} from 'vuex'
  export default {
    name: "List",
    components: {Pager},
    props:{
      editRoute:Object,
      showDel:false,
      isPager: {
        type:Boolean,
        default: true
      }
    },
    computed:{
      ...mapState('table',{
        pageSize: state => state.pageSize,
        start: state => state.start,
        list: state => state.list,
        total: state => state.total
      })
    },
    mounted(){
      if (!this.isPager){
        this.$store.commit('table/SET_PAGESIZE', 9999)
      }
    },
    methods:{
      removeData(){
        const pk = this.$store.state.table.checkbox
        if (pk.length === 0 ){
          this.$toast.warn({
            title:'操作错误~',
            message:'请勾选要删除的数据~'
          })
          return
        }
        this.$swal({
          title: this.$t('swal.title'),
          text: this.$t('swal.text'),
          type: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          cancelButtonText: this.$t('swal.cancelButtonText'),
          confirmButtonText: this.$t('swal.confirmButtonText')
        }).then((result) => {
          if (result.value) {
            this.$store.dispatch('table/remove',pk).then( resp =>{
              if (resp){
                this.$swal(
                  this.$t('swal.deleted'),
                  this.$t('swal.deletedText'),
                  'success'
                )
              }
            })
          }
        })
      },
      changePage(page){
        const length = this.pageSize
        const start = (page-1) * length
        this.$store.commit('table/SET_START', start)
        this.$store.dispatch('table/fetchList')
      },
      changeSize(size){
        this.$store.commit('table/SET_PAGESIZE', size)
        this.changePage(1)
      }
    }
  }
</script>
<style>
  table tr td{
    vertical-align:middle !important;
  }
</style>



