<template>
  <div class="form-group">
    <label class="col-sm-2 control-label">{{type === 'status'?$t('status.name'):label}}</label>
    <div class="col-sm-7">
      <input v-if="type === 'input' " v-model="val" class="form-control"
             autocomplete="off" :placeholder="label" :disabled="disabled">
      <input v-if="type === 'password' " type="password" v-model="val" class="form-control"
             :placeholder="label">

      <el-select v-if="type === 'select'" clearable v-model="val" :placeholder="label" style="width: 100%">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.text"
          :disabled="item.disabled || item.value === value "
          :value.sync="item.value">
        </el-option>
      </el-select>

      <el-date-picker v-if="type === 'date'"
        v-model="val"
        type="date"
        style="width: 100%;"
        value-format="yyyy-MM-dd"
        placeholder="选择日期">
      </el-date-picker>

      <mavon-editor v-if="type === 'editor'" ref="md"
                    @imgAdd="$imgAdd"
                    :toolbars="toolbars" v-model="val" />

      <div v-if="type === 'image'" class="input-group">
        <input class="form-control" :placeholder="label" :value="image_name">
        <span class="input-group-btn">
            <a target="_blank" style="margin:0 10px;">
                <img :src="image_src" style="height:34px; width:68px;">
            </a>
            <button class="btn btn-success btn-flat up_btn" type="button">
                <i class="fa fa-cloud-upload" > {{$t('status.upload')}}</i>
                <input type="file" class="file_btn"
                       accept="image/*" @change="upload">
            </button>
        </span>
      </div>

      <div v-if="type === 'inline'" class="checkbox-inline"  >

        <template v-for="(flag, key) in $t('flags')">
          <checkbox :value="key" v-model="val" />
          <span style="margin-right: 10px; margin-left: 5px">{{flag}}</span>
        </template>
      </div>

      <textarea v-if="type === 'textarea'" class="form-control" v-model="val"
                :placeholder="label" style="resize:none;height:155px;"></textarea>

      <el-select v-if="type === 'status'" v-model="val" style="width: 100%">
        <el-option
          v-for="item in status_opt"
          :key="item.value"
          :label="item.text"
          :value="item.value">
        </el-option>
      </el-select>
      <slot></slot>
    </div>
  </div>
</template>

<script>
    import Checkbox from "./Checkbox"
    import request from '@/utils/request'
    import { mavonEditor } from 'mavon-editor'
    import 'mavon-editor/dist/css/index.css'

    export default {
      name: "Group",
      components: {Checkbox, mavonEditor},
      props:{
        type:String,
        label:String,
        disabled:Boolean,
        options:Array,
        value:'',
      },
      data(){
        return {
          image_src:require('@/assets/no-image.png'),
          val:this.value,
          image_name:'',
          status_opt:[
            { value:-1, text:this.$t('status.disable')},
            { value:1, text:this.$t('status.use')},
          ],
          toolbars: {
            bold: false, // 粗体
            italic: false, // 斜体
            header: false, // 标题
            underline: false, // 下划线
            strikethrough: true, // 中划线
            mark: true, // 标记
            superscript: true, // 上角标
            subscript: true, // 下角标
            quote: true, // 引用
            ol: true, // 有序列表
            ul: true, // 无序列表
            link: false, // 链接
            imagelink: true, // 图片链接
            code: true, // code
            table: true, // 表格
            fullscreen: true, // 全屏编辑
            readmodel: true, // 沉浸式阅读
            htmlcode: true, // 展示html源码
            help: true, // 帮助
            /* 1.3.5 */
            undo: true, // 上一步
            redo: true, // 下一步
            trash: true, // 清空
            save: true, // 保存（触发events中的save事件）
            /* 1.4.2 */
            navigation: true, // 导航目录
            /* 2.1.8 */
            alignleft: true, // 左对齐
            aligncenter: true, // 居中
            alignright: true, // 右对齐
            /* 2.2.1 */
            subfield: true, // 单双栏模式
            preview: true, // 预览
          }
        }
      },
      watch:{
        value(val){
          this.val = val || ''
          if (this.type === 'inline'){
            if (val){
              if (!Array.isArray(val)){
                val = val.split(',')
              }
            } else{
              val = []
            }
            this.val = val
          }
          if (this.type === 'image' && val){
            if (val.constructor === String && val.length > 0){
              this.image_name = this.image_src = '/api/download/'+val
            }
          }
        },
        val(val){
          this.$emit('changeVal',val)
        }
      },
      model:{
        prop:this.value,
        event:'changeVal'
      },
      methods:{
        // 绑定@imgAdd event
        $imgAdd(pos, $file){
          // 第一步.将图片上传到服务器.
          const formdata = new FormData();
          formdata.append('filename', $file);
          request({
            url:'/api/upload',
            method:'POST',
            data: formdata,
            headers: { 'Content-Type': 'multipart/form-data' },
          }).then( resp =>{
            if (resp) {
              this.$refs.md.$img2Url(pos, resp.data)
            }
          })
        },
        upload(event){
          const file = event.target.files;
          const windowURL = window.URL
          if (file && file[0]){
            this.image_src = windowURL.createObjectURL(file[0])
            this.image_name = file[0].name
            this.val = file[0]
          }
        },
        select_change(val){
          this.val = val
        }
      }
    }
</script>
<style>
  .up_btn{
    position: relative;
    overflow: hidden;
    cursor: pointer;
  }
  .file_btn{
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
  }
  .tree-anchor{
    padding:0 4px 0 0 !important;
  }
  .v-note-wrapper.fullscreen{
    z-index: 9999 !important;
  }
  .v-note-wrapper{
    min-height: 400px !important;
    min-width: 0 !important;
    z-index: 0 !important;
  }
  .el-select-dropdown{
    z-index: 10 !important;
  }
</style>



