<template>
  <select class="form-control" title="" >
    <option v-for="opt in options" :selected="value == opt.value"
            :value="opt.value">{{opt.text}}</option>
  </select>
</template>

<script>
  import '@static/global/select2/select2.min'
  import '@static/global/select2/zh-CN'
    export default {
      name: "Select2",
      props:{
        options:Array,
        value:''
      },
      model:{
        prop:'value',
        event:'change'
      },
      mounted(){
        const _this = this

        const select2 =  $('select').select2({language:"zh-CN",width:'100%' })
          .on('select2:select',function () {
            const ret_val = _this.value || $(this).val()
            _this.$emit('change',$(this).val())
          })
        this.$nextTick( () =>{
          console.log(Array.isArray(this.options) )
          console.log(this.options)

        })
        console.log(!this.value)
        // if (Array.isArray(this.options) && !this.value){
        //   console.log(this.options[0].value)
        //
        //   // this.value = this.options[0].value
        // }
        console.log(this.value)

      }
    }
</script>



