<template>
    <td>
      <template v-if="type === 'status'">
        <a href="javascript:void(0);" v-if="value === 1"
           class="fa fa-check-circle text-green"></a>
        <a href="javascript:void(0);" v-else
           class="fa fa-times-circle text-red"></a>
      </template>
      <template v-if="type === 'opt'">
        <router-link class="btn btn-primary btn-xs" v-if="authRoute" :to="makeAuthRoute()">
          <i class="fa fa-edit"></i> {{authName}}</router-link>
        <a class="btn btn-primary btn-xs" v-if="jumpArchive || jumpArctype" @click="jumplink"  target="_blank">
          <i class="fa fa-eye"></i> {{$t('operation.preview')}}
        </a>
        <template v-if="model_id === 2">
          <span class="btn btn-default btn-xs disabled">
            <i class="fa fa-save"></i> {{$t('operation.saveArticle')}}</span>
        </template>
        <template v-else>
          <router-link class="btn btn-primary btn-xs" v-if="archiveRoute" :to="makeArchveRoute()">
            <i class="fa fa-save"></i> {{$t('operation.saveArticle')}}
          </router-link>
        </template>

        <router-link class="btn btn-primary btn-xs" v-if="editRoute" :to="makeEditRoute()" >
          <i class="fa fa-edit"></i> {{$t('operation.edit')}}
        </router-link>
        <a class="btn btn-warning btn-xs" v-if="showDownload"
           :href="'/api/database/backup/download/'+value">
          <i class="fa fa-download"></i> {{$t('operation.download')}}
        </a>
        <a class="btn btn-primary btn-xs delete" v-if="showRecovery" @click="recovery" >
          <i class="fa fa-rotate-left"></i> {{$t('operation.recovery')}}
        </a>
        <a class="btn btn-danger btn-xs delete" @click="deleteData">
          <i class="fa fa-trash"></i> {{$t('operation.delete')}}
        </a>
      </template>

    </td>
</template>

<script>
  import request from '@/utils/request'
  export default {
    props:{
      type:undefined,
      value:undefined,
      archiveRoute:undefined,
      editRoute:undefined,
      authRoute:undefined,
      authName:'',
      showDownload: false,
      showRecovery: false,
      model_id: 0,
      guestbook:undefined,
      jumpArctype: undefined,
      jumpArchive: undefined
    },
    data(){
      return {
        isOK: true
      }
    },
    methods:{
      jumplink(){
        const arctype = this.jumpArctype
        if (arctype){
          this.$store.commit('front/SET_TYPE_ID', arctype.id)
          this.$router.push({
            name: 'front_archive_list',
            params:{
              cname:arctype['dirs'] || arctype['typename'],
            }
          })
        } else{
          const article = this.jumpArchive
          this.$store.commit('front/SET_TYPE_ID', article.type_id)
          this.$router.push({
            name: 'detail_page',
            params: {id: article.id}
          })
        }
      },
      recovery(){
        if (!this.isOK){ return }
        this.isOK = false
        request.put('/api/database/backup/recovery/'+this.value).then( resp =>{
          if (resp){
            this.$toast.success({
              title: '数据库还原 ~',
              message: resp.error
            })
          }
          this.isOK = true
        })
      },
      makeAuthRoute(){
        return {
          name:this.authRoute,
          query:{ id: this.value}
        }
      },
      makeArchveRoute(){
        return {
          name: this.archiveRoute,
          query:{ type_id: this.value}
        }
      },
      makeEditRoute(){
        return {
          name:this.editRoute,
          query:{id:this.value, guestbook:this.guestbook},
        }
      },

      deleteData(){
        this.$swal({
          title: this.$t('swal.title'),
          text: this.$t('swal.text'),
          type: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          cancelButtonText: this.$t('swal.cancelButtonText'),
          confirmButtonText: this.$t('swal.confirmButtonText')
        }).then((result) => {
          if (result.value) {
            this.$store.dispatch('table/remove',this.value).then( resp =>{
              if (resp){
                this.$swal(
                  this.$t('swal.deleted'),
                  this.$t('swal.deletedText'),
                  'success'
                )
              }
            })
          }
        })
      }
    }
  }
</script>



