<template>
  <ul v-if="total > pageSize" class="pagination pagination-sm no-margin pull-right ">
    <li class="disabled">
      <span>{{total}} 条数据</span>
    </li>
    <li :class="{disabled:showPrevPage}" @click="prevPage">
      <a>上一页</a>
    </li>

    <li v-for="index in list" :class="{active:currentPage === index,disabled: index === 0}"
        @click="jumpPage(index)">
      <span v-if="index === 0">...</span>
      <a v-else>{{index}}</a>
    </li>

    <li :class="{disabled:showNextPage}" @click="nextPage">
      <a>下一页</a>
    </li>
    <li>
      <select title="" v-model="selected" >
        <option v-for="index in pages" :value="index">{{index}}</option>
      </select>
    </li>
    <li>每页显示数量 <input type="number" v-model="size" :max="total" min="size" class="size" title=""></li>

  </ul>
</template>

<script>
    export default {
      name: "Pager",
      props:{
        total:Number, // 数据总条数
        pageSize:Number // 每页显示条数
      },
      data(){
        return{
          currentPage: 1, // 当前页
          size:this.pageSize,      // 每页显示条数
          selected:1,   // 选择的页码
        }
      },
      watch:{
        // 选择前往页码监听 返回父组件页码改变事件
        selected(val){
          this.currentPage = val
        },
        // 每页显示条数监听 返回父组件页码改变监听 页码 = 1
        size(val){
          val = Number(val)
          this.size = val > 0 ? val:this.pageSize
          this.currentPage = 1
          this.$emit('changeSize',this.size)
        },
        currentPage(val){
          this.$emit('changePage',val)
        }
      },
      computed:{
        // 总页数
        pages(){
          if (this.size === 0) { return 0}
          return Math.ceil(this.total / this.size )
        },
        // 是否禁用上一页
        showPrevPage(){
          return this.currentPage <= 1;
        },
        // 是否禁用下一页
        showNextPage(){
          return this.currentPage >= this.pages;
        },
        // 是否显示 ...
        showEllipsis(){
          if (this.pages <= 7) return false
          return this.currentPage < 5
        },
        // 初始化页码列表
        list(){
          let left = 1, right = this.pages,
            pageCount = this.pages
          const currentPage = this.currentPage
          let array = []
          if (pageCount >= 7){
            if (currentPage > 5 && currentPage < pageCount - 4){
              left = Number(currentPage) - 3
              right = Number(currentPage) + 3
            } else {
              if (currentPage <= 5 ){
                left = 1
                right = 7
              } else {
                right = pageCount
                left = pageCount - 6
              }
            }
          }
          // 是否显示 1
          if (currentPage > 5 ) array.push(1)
          // 是否显示 ...
          if (left > 2) array.push(0)
          // 组装left -> right 的数组
          while (left <= right){
            if (array[0] !== left) {
              array.push(left)
            }
            left ++
          }
          // 是否显示 ...
          if (right < pageCount) array.push(0)
          // 是否显示 最后一页
          if (currentPage < pageCount - 4 && pageCount > 7 ) array.push(pageCount)
          return array
        }
      },
      methods:{
        // 上一页点击事件
        prevPage(){
          if (this.currentPage > 1){
            this.currentPage --
          }
        },
        // 下一页点击事件
        nextPage(){
          if (this.currentPage < this.pages){
            this.currentPage ++
          }
        },
        // 页码点击事件
        jumpPage(index){
          if (index !== 0) {
            this.currentPage = index
          }
        }
      }
    }
</script>

<style>
  .pagination input.size{
    width: 50px;
    border: 1px solid #ddd;
    text-align: center;
    border-radius: 3px;
    padding: 0 5px;
    height: 30px;
    line-height: 30px;
    display: inline-block;
  }
  .pagination select {
    height: 30px;
    line-height: 30px;
    padding: 0 5px;
    border: 1px solid #ddd;
    border-left: none;
    display: inline-block;
    cursor: pointer;
  }
  .pagination>li>a {
    cursor: pointer;
  }
</style>



