<template>
  <label class="iCheck-box" >
    <input type="checkbox" @change="change" :checked="state" >
    <ins class="iCheck-help" :class="{checked:state}" ></ins>
  </label>
</template>

<script>
  export default {
    name: "Checkbox",
    props:{
      checked:false,
      value:'',
      modelValue:undefined
    },
    model:{
      prop:'modelValue',
      event:'input'
    },
    computed:{
      state(){
        if (this.modelValue === undefined){
          return this.checked
        }
        if (Array.isArray(this.modelValue)){
          return this.modelValue.indexOf(this.value) > -1
        }
        return !!this.modelValue
      }
    },
    methods:{
      change(){
        this.toggle()
      },
      toggle(){
        let value;
        if (Array.isArray(this.modelValue)){
          value = this.modelValue.slice(0)
          if (this.state){
            value.splice(value.indexOf(this.value), 1)
          } else{
            value.push(this.value)
          }
        } else{
          value = !this.state
        }
        this.$emit('input',value)
      }
    },
    watch:{
      checked(newValue){
        if (newValue !== this.state){
          this.toggle()
        }
      }
    },
    mounted(){
      if (this.checked && !this.state){
        this.toggle()
      }
    }

  }
</script>

<style scoped>
  .iCheck-box{
    display: inline-block;
    position: relative;
    width: 22px;
    vertical-align: top;
    height: 22px;

    padding: 0;
    margin: 0;
    border: none;
    cursor: pointer;
  }
  .iCheck-box > .iCheck-help:hover{
    background-position: -24px 0;

  }
  .iCheck-box > .iCheck-help.checked{
    background-position: -48px 0;
  }
  .iCheck-box>input[type=checkbox]{
    opacity: 0;
    position: absolute;
  }
  .iCheck-box > .iCheck-help{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height:100%;
    display: block;
    background: url('../../assets/checkbox/blue.png') no-repeat;
  }
</style>



