<template>
  <div class="content-wrapper" style="height: auto;">
    <ContentHead :title="title"/>
    <section class="content" style="min-height: 580px" >
      <div class="row">
        <div class="col-md-12">
          <slot></slot>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
    import ContentHead from "./ContentHead"
    export default {
      name: "container",
      components: {ContentHead},
      props:{
        title:String,
      }
    }
</script>

<style scoped>

</style>



