<template>
    <form class="form-horizontal" @submit.prevent="$emit('submit')">
      <div class="nav-tabs-custom">
        <ul class="nav nav-tabs">
          <li class="active"><a href="#baseParam" data-toggle="tab">{{$t('navTab.baseParam')}}</a></li>
          <li v-if="advanced"><a href="#advancedParam" data-toggle="tab">{{$t('navTab.advancedParam')}}</a></li>
          <li class="pull-right">
            <a href="javascript:history.back(-1)" class="btn btn-sm" style="padding:10px 2px;">
              <i class="fa fa-list"></i> {{$t('navTab.back')}}</a></li>
        </ul>
        <div class="tab-content">
          <div class="tab-pane active" id="baseParam">
            <slot name="base"></slot>
          </div>

          <div class="tab-pane" v-if="advanced" id="advancedParam">
            <slot name="advanced"></slot>
          </div>
        </div>
        <div class="box-footer">
          <div class="col-sm-2"></div>
          <div class="col-sm-7">
            <div class="btn-group pull-right" >
              <button type="submit" class="btn btn-info pull-right submits">{{$t('status.submit')}}</button>
            </div>
            <div class="btn-group pull-left" style="margin-left: -8px">
              <button type="reset" class="btn btn-warning">{{$t('status.reset')}}</button>
            </div>
          </div>
        </div>
      </div>
    </form>
</template>

<script>
    import Container from "../layouts/Container"
    export default {
      name: "VForm",
      components: {Container},
      props:{
        advanced:false
      }
    }
</script>



