<template>
   <li :class="{'treeview':isChildren}">
    <router-link :to="{ name:model.name}">
      <i :class="model.meta.icon"></i><span>{{getMenuName}}</span>
      <span class="pull-right-container" v-if="isChildren">
        <i class="fa fa-angle-left pull-right"></i></span>
    </router-link>
    <ul class="treeview-menu" v-if="isChildren">
      <tree-menu
        v-for="(model, index) in model.children"
        v-if="model.isMenu"
        :key="index"
        :model="model">
      </tree-menu>
    </ul>
  </li>
</template>

<script>
    export default {
      name: "TreeMenu",
      props: {
        model:Object
      },
      computed:{
        isChildren:function () {
          if (!this.model.hasOwnProperty('children')){
            return false
          }
          const len = this.model.children.length
          return !(len === 1 || len === 0);
        },
        getMenuName:function () {
          if (this.model.meta.hasOwnProperty('title')){
            return this.$t('route.'+this.model.meta.title)
          }
        }
      }
    }
</script>



