<template>
  <div class="box box-primary">
    <div class="box-header">
      <h3 class="box-title">{{ title }}</h3>
    </div>
    <div class="box-body no-padding">
      <table class="table table-hover">
        <thead>
          <tr><th v-for="col in columns" :key="col.id" v-html="col">{{col}}</th></tr>
        </thead>
        <tbody>
        <template v-if="Array.isArray(data)">
          <tr v-for="item in data" :key="item.id">
            <td v-for="child in item " :key="child.id" v-html="child">
              {{ child }}
            </td>
          </tr>
        </template>
        <template v-else>
          <tr v-for="item in data" :key="item.id">
            <td v-html="item">{{item}}</td>
          </tr>
        </template>

        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
    export default {
      name: "Box",
      props:['title','data','columns']
    }
</script>

<style scoped>

</style>



