<template>
  <div :class="tp">
    <div class="inner">
      <h3>{{number}}</h3>
      <p>{{$t('smallBox.'+title)}}</p>
    </div>
    <div class="icon"><i :class="icon"></i></div>

    <router-link :to="{ name:url }" class="small-box-footer">
      {{$t('smallBox.more')}} <i class="fa fa-arrow-circle-right"></i>
    </router-link>

  </div>
</template>

<script>
  export default {
    name: "SmallBox",
    props:{
      title:String,
      number:Number,
      url:String
    },
    data(){
      const array = {
        user:{type:'small-box bg-yellow',icon:'fa fa-users'},
        article:{type:'small-box bg-green', icon:'fa fa-file-text'},
        userNear:{type:'small-box bg-aqua',icon:'fa fa-user-plus'},
        comment:{type:'small-box bg-red',icon:'fa fa-commenting'}
      }
      const title = this.title
      return {
        tp:array[title].type,
        icon:array[title].icon
      }
    },

  }
</script>



